/** @format */

import React from "react";
import classes from "./NoFacebookPage.module.css";
import sadIcon from "../../../../assets/icons/sadicon.svg";
import { CustomButton } from "../../../exports";

const NoFacebookPage = (props) => {
  return (
    <div className={classes.Type0_Account}>
      <div className={classes.Type0_Note}>
        <img src={sadIcon} alt="sad" />
        <p>We could not find any Facebook page accounts.</p>
        <main className={classes.Type0_Link}>
          <CustomButton
            className={classes.Type0_Button}
            onClick={
              () => window.open(props.setupRedirectURL, "_blank")
              // window.open(`https://www.facebook.com/pages/creation`, "_blank")
            }
          >
            Connect Instagram
          </CustomButton>
        </main>
      </div>
      <p className={classes.Done_Linking}>
        Done linking?{" "}
        <span
          onClick={() => {
            props.history.push(`/business-setup/link-facebook`);
          }}
        >
          Resume Setup!
        </span>
      </p>
    </div>
  );
};

export default NoFacebookPage;
